<template>
  <div class="beijinTY">
   <fg-header :title="title"></fg-header>
   <user-info class="user-info marT" uStar ></user-info>
   <div class="marT">
     <ul class="listul">
       <li class="listLi" v-for="item in listAll" :key='item.id'>
         <div class="neibubg">
           <div class="imgdiv">
             <img class="listimg" :src="item.image" alt="">
             <div class="listbtn yichufa" v-if="item.state=='working'"><span class="buttext" >已出发</span></div>
             <div class="listbtn chufa"   v-if="item.state=='free'"><span class="buttext" @click='chufa(item)'>出发</span></div>
           </div>
           <div class="listtext">
             <p>名称： {{item.level}}</p>
             <p>产出：{{item.contribution}}</p>
             <p>剩余时间：永久</p>
              <!-- {{item.left_secs}} -->
             <p>人数： {{item.amount}}</p>
             <p>状态：{{item.state}}</p>
           </div>

         </div>
       </li>
   
     </ul>
   </div>
    <van-popup v-model="popupshow">
        <div class="yaoqingbgBG" >
          <h1 class="yaoqingtitle topmar">使用道具</h1>
          <p v-if="bombslistall.length==0" class="nodata mart80">暂无道具</p>
          <ul class="copy_text popul">
            <li class="boxp" v-for="item in bombslistall" :key="item.id">
              <div class="imgdiv imgdivA">
                <img class="listimg "  :src='item.image' alt="">
              </div>
              <div class="listtext listtextA">
                <p>{{item.name}}</p>
                <p>加成{{item.increase_percent}}</p>
                <p>数量{{item.amount}}</p>
                <div class='fuzhi shiyong' @click="goTo('produce',item)">使用</div>
              </div>
           </li>
          </ul>
          <div class="btnline shiyongbtns">
            <div class="bushiyong"  @click="goTo('produce','no')">不用道具，直接出发</div>
            <div  @click="popupshow=false">取消</div>
          </div>
        </div>
    </van-popup>
     <!-- <van-popup v-model="popupshow">
        <div class="yaoqingbg" >
          <h1 class="yaoqingtitle">提示</h1>
          <div class="copy_text">是否确定要出发？</div>
          <div class="btnline">
