  </div>
</template>
<script type='text/ecmascript-6'>
import { user_toolslist_my} from '@/api/tools'
import { soldiersall, start_working } from '@/api/guardian'
export default {
  components: {
  },

  data() {
    return {
       title:'',
       popupshow:false,
       listAll:[],
       bombslistall:[],
       chufaitem:''
    };
  },
  created() {
  },
  mounted() {
    if(this.$route.params.title){
      localStorage.setItem('titlename',this.$route.params.title);
      this.title=this.$route.params.title
    }else{
      setTimeout(() => {
        this.title= localStorage.getItem('titlename')
     }, 100);
    }
    this.bombslist_all()
    this.soldiersall();
   
  },
  methods: {
    chufa(item){
      this.chufaitem=item
      this.popupshow=true
      
    },
     goTo(name,type) {
       if(type=='no'){
         let data={
           id:this.chufaitem.id,
           ...this.$datapublic
         }
          start_working(data).then((res) => {
            if(res.data.code=='success'){
               this.$toast('派遣成功')
                this.$router.push({ name }) 
            }else{this.$toast(res.data.msg)}
             
          }).catch((error) => { reject(error)})
       }else{
          let data={
           id:this.chufaitem.id,
           user_tool_id:type.id,
           lang:localStorage.getItem('lang'),
      	   user_token:localStorage.getItem('user_token')
         }
          start_working(data).then((res) => {
            if(res.data.code=='success'){
               this.$toast('派遣成功')
                this.$router.push({ name }) 
            }else{this.$toast(res.data.msg)}
             
          }).catch((error) => { reject(error)})
       }
       
      },
    soldiersall(){
      soldiersall().then((res) => {
        if(res.data.code=='success'){this.listAll=res.data.data}else{this.$toast(res.data.msg)}
        
          }).catch((error) => {
            reject(error)
          })
    },
    bombslist_all(){
      user_toolslist_my().then((res) => {
        if(res.data.code=='success'){ this.bombslistall=res.data.data    }else{this.$toast(res.data.msg)}
              
      }).catch((error) => { reject(error)})
                   
    },
   
  },
};
</script>
